/* index.css */
@import-normalize; /* bring in normalize.css styles */

:root {
  /* colors */
    --white-color: #ffffff6a;
    --mobile-color: #8702028a;
    --tablet-color: #0080006f;
    --desktop-color: #02318790; 
    --background-color: #0c0c0c;
    --main-text-color: #8d8c76;
    --non-active-color: #3f3e33;
    --secondary-text-color: #073;
    --hover-text-color: red;
    --accent-text-color: #944;
    --master-container-bg-color: #2d2c2c63;
    --mobile-background-color: #14090927;
    --tablet-background-color: #0077344e;
    --desktop-background-color: #00669952;
/* fonts */
    --primary-font: 'MainFont';
    --secondary-font: 'MainFont';
/* breakpoints */
    --mobile: 480px;
    --mobile-max: 767.9px;
    --tablet: 768px;
    --tablet-max: 1199.9px;
    --desktop: 1200px;
}


* {
  list-style-type: none;
  text-decoration: none;
  text-align: justify;
}

ul {
padding: 0;
margin: 0;
}

body, html {
  height: 100%;
}

a {
color: var(--non-active-color);
font-weight: 900;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'PrimaryFont';
  font-weight: 100;
  background: var(--background-color);
  color: var(--main-text-color);

  font-size: 10px;
}


.content {
  flex: 1;
}


@font-face {
  font-family: PrimaryFont;
  src: url(./data/fonts/Constantine.ttf) format("truetype");
}



.container {
  box-sizing: border-box;
  margin: 0 auto;
  padding: 10px 0;

  @media screen and (min-width: 768px) {
   display: inline-flex;
   padding: 0;
   width: 100%;
   height: 80px;
 }

 @media screen and (min-width: 1200px) {
   display: inline-flex;
   padding: 0;
   width: 100%;
   height: 80px;
 }
};
